.modalFranchise {
  background-color: #FCFCFC;
  border: 2px solid #F8D24E;
  border-radius: 10px;
  max-width: 568px;
  padding: 32px 60px;
  position: relative;
  width: 100%;
}

.modalFranchise__title {
  max-width: 322px;
  margin: 0 auto 24px;
}

.modalFranchise__text {
  font-size: 16px;
  line-height: lh(16,22);
  margin-bottom: 32px;
}


@mixin modalFranchise-576 {

  .modalFranchise {
    padding: 28px;
  }

  .modalFranchise__title {
    margin-bottom: 20px;
  }

  .modalLocation__text {
    font-size: 14px;
    line-height: lh(14,20);
    margin-bottom: 24px;
  }

}