@import "../fonts.scss";

.wrapper {
  color: #131313;
  display: flex;
  flex-direction: column;
  font-family: Montserrat, Arial, sans-serif;
  min-height: 100vh;
  position: relative;

  *,
  *::before,
  *::after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }

  a:not(.outline),
  input:not(.outline),
  textarea:not(.outline),
  button:not(.outline) {
    outline: none;
  }

  a:not(.textDecoration) {
    text-decoration: none;
  }

  ul:not(.listStyle) {
    list-style: none;
  }

  img {
    object-fit: cover;
  }

}

.container {
  margin: 0 auto;
  max-width: 1210px;
  padding: 0 15px;
  width: 100%;
}

.myMain {
  flex-grow: 1;
}


@mixin common-768 {

  .container {
    padding: 0 32px;
  }

}


@mixin common-576 {

  .container {
    padding: 0 20px;
  }

}