.closeModal {
  background-color: transparent;
  border: none;
  height: 20px;
  position: absolute;
  right: 24px;
  top: 24px;
  width: 20px;

  &::before,
  &::after {
    background-color: #131313;
    content: "";
    display: block;
    height: 1px;
    left: 0;
    position: absolute;
    top: 50%;
    width: 100%;
  }

  &::before {
    transform: rotate(45deg) translateY(-50%);
  }

  &::after {
    transform: rotate(-45deg) translateY(-50%);
  }
}


@mixin closeModal-576 {

  .closeModal {
    height: 16px;
    right: 11px;
    top: 11px;
    width: 16px;
  }

}