.modalMade {
  background-color: #FCFCFC;
  border: 2px solid #F8D24E;
  border-radius: 10px;
  max-width: 568px;
  padding: 32px 58px;
  position: relative;
  width: 100%;
}

.modalMade__title {
  margin-bottom: 32px;
}

.modalMade__video {
  align-items: center;
  background-color: #000000;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  height: 205px;
  margin-bottom: 32px;
}

.modalMade__text {
  font-size: 16px;
  line-height: lh(16,22);
}


@mixin modalMade-576 {

  .modalMade {
    padding: 28px;
  }

  .modalMade__title {
    margin-bottom: 20px;
  }

  .modalMade__video {
    margin-bottom: 20px;
  }

  .modalMade__text {
    font-size: 14px;
    line-height: lh(14,20);
  }

}