.modalProductCards {
  background-color: #FCFCFC;
  border: 2px solid #F8D24E;
  border-radius: 10px;
  max-width: 568px;
  padding: 32px 40px;
  position: relative;
  width: 100%;
}

.modalProductCards__card {

}

.modalProductCards__cardBody {
  align-items: center;
  display: flex;
  flex-direction: column;
  margin-bottom: 48px;
}

.modalProductCards__cardTitle {
  margin-bottom: 31px;
}

.modalProductCards__cardImg {
  background: linear-gradient(0deg, rgba(156, 190, 143, 0.7), rgba(156, 190, 143, 0.7)), #D9D9D9;
  box-shadow: 0 5px 19px rgba(10, 136, 23, 0.25);
  border-radius: 50%;
  height: 130px;
  margin-bottom: 24px;
  width: 130px;

  img {
    border-radius: 50%;
    margin-top: -7px;
  }
}

.modalProductCards__cardEnergy {
  display: flex;
  margin-bottom: 48px;
}

.modalProductCards__cardEnergyItem {
  align-items: center;
  border-style: solid;
  border-width: 2px;
  border-radius: 40px;
  display: flex;
  flex-direction: column;
  height: 90px;
  margin-right: 20px;
  width: 65px;

  &:last-child {
    margin-right: 0;
  }
}

.modalProductCards__cardEnergyItemNum {
  align-items: center;
  background: #9CBE8F;
  border-radius: 50%;
  box-shadow: 0 6px 20px rgba(10, 136, 23, 0.25);
  display: flex;
  justify-content: center;
  height: 53px;
  font-size: 12px;
  line-height: lh(12,28);
  margin: -2px 0 7px;
  overflow: hidden;
  width: 53px;
}

.modalProductCards__cardEnergyItemTitle {
  font-size: 12px;
  line-height: lh(12,15);
}

.modalProductCards__cardIngredients {
  align-self: stretch;
}

.modalProductCards__cardIngredientsTitle {
  font-size: 20px;
  font-weight: 500;
  line-height: lh(20,28);
  margin-bottom: 24px;
  text-align: center;
}

.modalProductCards__cardIngredientsBody {
  font-size: 14px;
  line-height: lh(14,25);
  letter-spacing: 0.04em;
}

.modalProductCards__cardIngredient {
  display: flex;
  justify-content: space-between;
  margin-bottom: 11px;

  &:last-child {
    margin-bottom: 0;
  }
}

.modalProductCards__cardIngredientTitle {

}

.modalProductCards__cardIngredientNum {

}

.modalProductCards__sendForm {

  input[name="userEmail"] {
    display: none;
  }

}


@mixin modalProductCards-576 {

  .modalProductCards {
    padding: 28px 20px;
  }

  .modalProductCards__cardTitle {
    margin-bottom: 29px;
  }

  .modalProductCards__cardEnergy {
    margin-bottom: 39px;
  }

  .modalProductCards__cardEnergyItem {
    height: 72px;
    margin-right: 9px;
    width: 53px;
  }

  .modalProductCards__cardEnergyItemNum {
    height: 43px;
    font-size: 10px;
    line-height: lh(10,28);
    width: 43px;
  }

  .modalProductCards__cardEnergyItemTitle {
    font-size: 10px;
    line-height: lh(10,12);
  }

}