.made {
  background-color: #84A178;
  padding: 60px 0 180px;
}

.made__body {
  overflow: hidden;
}

.made__subtitle {
  color: #FCFCFC;
  margin-bottom: 12px;
  text-align: center;
}

.made__title {
  color: #FCFCFC;
  margin-bottom: 82px;
  text-align: center;
}

.made__content {
  align-items: center;
  display: flex;
  justify-content: space-between;
  margin-bottom: 89px;
}

.made__img {
  display: flex;
  background: linear-gradient(0deg, rgba(248, 210, 78, 0.7), rgba(248, 210, 78, 0.7)), #D9D9D9;
  border-radius: 50%;
  box-shadow: 0 8px 30px rgba(153, 114, 23, 0.25);
  justify-content: center;
  flex-shrink: 0;
  height: 500px;
  margin-right: 32px;
  width: 500px;

  img {
    border-radius: 50%;
    height: 490px;
    margin-top: 39px;
    width: 490px;
  }
}

.made__contentBody {
  flex-grow: 1;
}

.made__contentWrapper {
  max-width: 463px;
  min-width: 220px;
  margin: 0 auto;
}

.made__text {
  color: #FCFCFC;
  font-size: 14px;
  line-height: lh(14,27);
  letter-spacing: 0.04em;
  margin-bottom: 48px;

  p {
    margin-bottom: 8px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}

.made__info {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  margin: -15px -60px;
}

.made__infoItem {
  align-items: center;
  display: flex;
  flex-direction: column;
  margin: 15px 60px;
  width: 279px;
}

.made__infoItemImg {
  align-items: center;
  background-color: #F8D24E;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  height: 50px;
  margin-bottom: 40px;
  width: 50px;
}

.made__infoItemTitle {
  color: #FCFCFC;
  font-weight: 700;
  font-size: 58px;
  line-height: lh(58,71);
  letter-spacing: 0.04em;
  margin-bottom: 16px;
  text-align: center;
}

.made__infoItemText {
  color: #FCFCFC;
  font-size: 16px;
  line-height: lh(16,28);
  text-align: center;
  letter-spacing: 0.02em;
}


@mixin made-992 {

  .made__img {
    height: 400px;
    width: 400px;

    img {
      height: 100%;
      margin-top: 30px;
      width: 100%;
    }
  }

}


@mixin made-768 {

  .made {
    padding: 60px 0 200px;
  }

  .made__title {
    margin-bottom: 72px;
  }

  .made__content {
    flex-wrap: wrap;
    justify-content: center;
    margin: -32px -15px 42px;
  }

  .made__img {
    height: 330px;
    margin: 32px 15px;
    width: 330px;

    img {
      margin-top: 25px;
    }
  }

  .made__contentBody {
    margin: 32px 15px;
  }

  .made__contentWrapper {
    max-width: 334px;
  }

  .made__text {
    font-size: 12px;
    line-height: lh(12,21);
  }

  .made__info {
    margin: -15px -40px;
  }

  .made__infoItem {
    margin: 15px 40px;
    width: 152px;
  }

  .made__infoItemImg {
    margin-bottom: 32px;
  }

  .made__infoItemTitle {
    font-size: 48px;
    line-height: lh(48,59);
  }

  .made__infoItemText {
    font-size: 14px;
    line-height: lh(14,24);
  }

}


@mixin made-576 {

  .made {
    padding: 60px 0;
  }

  .made__title {
    margin-bottom: 32px;
  }

  .made__content {
    margin-bottom: 10px;
  }

  .made__img {
    height: 278px;
    width: 278px;

    img {
      margin-top: 21px;
    }
  }

  .made__contentWrapper {
    text-align: center;
  }

  .made__text {
    margin-bottom: 20px;

    p {
      margin-bottom: 3px;
    }
  }

  .made__info {
    align-items: center;
    flex-direction: column;
    margin: -15px 0;
  }

  .made__infoItem {
    margin: 15px 0;
    width: 230px;
  }

  .made__infoItemImg {
    margin-bottom: 16px;
  }

  .made__infoItemTitle {
    font-size: 36px;
    line-height: lh(36,42);
    margin-bottom: 18px;
  }

  .made__infoItemText {
    font-size: 12px;
    line-height: lh(12,20);
  }

}


@mixin made-320 {

  .made__contentWrapper {
    width: 220px;
  }

}