.sendForm {
  margin: 0 auto;
  max-width: 368px;
  text-align: center;

  input,
  textarea {
    background: rgba(248, 210, 78, 0.1);
    border: 1px solid #84A178;
    border-radius: 10px;
    color: #131313;
    font-family: Montserrat, sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: lh(14,20);
    padding: 11px 16px;

    @include inputPlaceholder(14,#131313,1);
  }

  input {
    margin-bottom: 16px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  textarea {
    min-height: 84px;
    resize: vertical;
  }

}

.sendForm__inputs {
  display: flex;
  flex-direction: column;
  margin-bottom: 32px;
}


@mixin sendForm-576 {

  .sendForm__inputs {
    margin-bottom: 24px;
  }

  input,
  textarea {
    padding: 8px 12px;
  }

  input {
    margin-bottom: 12px;
  }

}