.welcome {
  background: url(/assets/src/img/welcome/points.png) no-repeat 50% 95%, #84A278;
}

.welcome__body {
  display: flex;
  justify-content: space-between;
  padding-top: 74px;
  position: relative;
}

.welcome__wrapperFirst {
  position: relative;
  z-index: 1;
}

.welcome__title {
  color: #FCFCFC;
  font-family: SeymourOne, sans-serif;
  font-weight: 400;
  font-size: 38px;
  line-height: lh(38,48);
  letter-spacing: 0.04em;
  margin: 44px 0 28px;
  max-width: 370px;
  text-transform: uppercase;

  span {
    display: block;
  }
}

.welcome__nav {
  margin-bottom: 42px;
}

.welcome__btn {
  margin-right: 34px;
}

.welcome__link {
  color: #F8D24E;
  font-weight: 600;
  font-size: 14px;
  line-height: lh(14,17);
  transition: color 0.3s;

  &:hover,
  &:focus {
    color: #fcfcfc;
  }
}

.welcome__info {
  align-items: center;
  display: flex;
}

.welcome__infoImg {
  flex-shrink: 0;
  height: 225px;
  margin-right: 14px;
  width: 225px;
}

.welcome__infoContainer {

}

.welcome__infoText {
  color: #FCFCFC;
  font-weight: 400;
  font-size: 14px;
  line-height: lh(14,20);
  margin-bottom: 32px;
  max-width: 200px;
}

.welcome__infoArrow {
  transform-origin: left;
  width: 40px;

  svg {
    width: 100%;
  }
}

.welcome__wrapperSecond {
  display: flex;
  height: 590px;
  position: relative;
}

.welcome__img {
  bottom: -104px;
  right: 0;
  position: absolute;

  img {
    height: 765px;
    width: 642px;
  }
}

.welcome__stores {
  margin-top: 52px;
  position: relative;
}

.welcome__storesItem {
  margin-bottom: 16px;
  text-align: right;

  &:last-child {
    margin-bottom: 0;
  }
}

.welcome__storesNumber {
  color: #131313;
  font-weight: 900;
  font-size: 46px;
  line-height: lh(45,56);
  letter-spacing: 0.04em;
  margin-bottom: 4px;
  text-transform: uppercase;
}

.welcome__storesName {
  color: #FCFCFC;
  font-weight: 400;
  font-size: 14px;
  line-height: lh(14,17);
}


@mixin welcome-992 {

  .welcome {
    background-position: 100% 100%;
    background-size: 100%;
  }

  .welcome__body {
    padding: 90px 0 50px;
  }

  .welcome__title {
    font-size: 28px;
    line-height: lh(28,33);

    span {
      font-size: 35px;
      line-height: lh(35,32);
    }
  }

  .welcome__btn {
    font-size: 12px;
    line-height: lh(12,14);
  }

  .welcome__link {
    font-size: 12px;
    line-height: lh(12,14);
  }

  .welcome__infoImg {
    height: 152px;
    width: 152px;
  }

  .welcome__infoText {
    font-size: 14px;
    line-height: lh(14,19);
  }

  .welcome__wrapperSecond {
    height: 340px;
  }

  .welcome__img {
    bottom: -195px;

    img {
      height: 565px;
      width: 470px;
    }
  }

  .welcome__storesNumber {
    font-size: 32px;
  }

  .welcome__storesName {
    font-size: 12px;
  }

}


@mixin welcome-768 {

  .welcome {
    background-position: 100% 80%;
    background-size: 90%;
  }

  .welcome__body {
    padding: 90px 0 0;
  }

  .welcome__title {
    font-size: 18px;
    line-height: lh(18,23);
    margin: 14px 0 20px;
    max-width: 185px;

    span {
      font-size: 16px;
      line-height: lh(16,13);
    }
  }

  .welcome__nav {
    margin-bottom: 28px;
  }

  .welcome__btn {
    font-size: 8px;
    line-height: lh(8,10);
    margin-right: 18px;
    padding: 5px 11px;
  }

  .welcome__link {
    font-size: 8px;
    line-height: lh(8,10);
  }

  .welcome__infoImg {
    margin-right: 33px;
  }

  .welcome__infoText {
    font-size: 12px;
    line-height: lh(12,17);
    margin-bottom: 20px;
    max-width: 151px;
  }

  .welcome__wrapperSecond {
    height: 325px;
  }

  .welcome__img {
    bottom: -58px;

    img {
      height: 440px;
      width: 376px;
    }
  }

  .welcome__stores {
    margin-top: 4px;
  }

  .welcome__storesItem {
    margin-bottom: 20px;
  }

  .welcome__storesNumber {
    font-size: 28px;
    line-height: lh(28,34);
  }

  .welcome__storesName {
    font-size: 8px;
    line-height: lh(8,10);
  }

}


@mixin welcome-576 {

  .welcome {
    background-position: 158% 99%;
    background-size: 120%;
  }

  .welcome__body {
    flex-direction: column;
    padding-top: 110px;
  }

  .welcome__wrapperFirst {
    margin-bottom: 20px;
  }

  .welcome__title {
    font-size: 26px;
    line-height: lh(26,32);
    margin: 0 0 32px;
    max-width: 260px;

    span {
      font-size: 34px;
      line-height: lh(34,32);
    }
  }

  .welcome__nav {
    margin-bottom: 0;
  }

  .welcome__info {
    display: none;
  }

  .welcome__wrapperSecond {
    justify-content: center;
    height: 220px;
  }

  .welcome__img {
    bottom: auto;
    position: relative;
    right: auto;

    img {
      bottom: 21px;
      height: 278px;
      position: relative;
      width: 234px;
    }
  }

  .welcome__stores {
    display: none;
  }

}


