.blockBtn--2 {
  background-color: transparent;
  border: 2px solid #F8D24E;

  svg {

    path {
      transition: stroke 0.3s;
    }
  }

  &:hover {
    border-color: #0A8817;
  }

  &:focus {
    border-color: #0A8817;
    color: #FCFCFC;

    svg {

      path {
        stroke: #FCFCFC;
      }
    }
  }

}