.slides {
  background: url(/assets/src/img/slides/bg.jpg) no-repeat center/cover;

  .slick-dots {

    li {
      border-radius: 50%;
      height: 12px;
      margin: 0 9px;
      overflow: hidden;
      width: 12px;

      &.slick-active {
        button {
          background-color: #84A178;
        }
      }

      button {
        background-color: #D9D9D9;
        height: 100%;
        width: 100%;

        &::before {
          display: none;
        }
      }
    }
  }
}

.slides__body {
  padding: 0 80px 95px;
}

.slides__slider {
  transform: translateY(-100px);
}

.slides__slide {

}

.slides__slideBody {
  align-items: center;
  display: flex;
  flex-direction: column;
  margin: 0 20px;
}

.slides__slidePicture {
  border-radius: 24px;
  height: 422px;
  margin: 0 0 128px;
  overflow: hidden;
  width: 100%;
}

.slides__slidePhoto {
  border-radius: 50%;
  height: 85px;
  margin-bottom: 32px;
  overflow: hidden;
  width: 85px;
}

.slides__text {
  background-color: #84A178;
  border-radius: 24px;
  color: #FCFCFC;
  font-weight: 500;
  font-size: 16px;
  line-height: lh(16,30);
  letter-spacing: 0.04em;
  margin-bottom: 32px;
  padding: 39px 73px;
  text-align: center;
}

.slides__note {
  font-weight: 500;
  font-size: 16px;
  line-height: lh(16,30);
  margin-bottom: 32px;
}


@mixin slides-768 {

  .slides__body {
    margin: 0 -20px;
    padding: 0 0 200px;
  }

  .slides__slider {
    transform: translateY(-165px);
  }

  .slides__slidePicture {
    border-radius: 18px;
    height: 327px;
    margin: 0 0 55px;
  }

  .slides__slidePhoto {
    margin-bottom: 28px;
  }

  .slides__text {
    font-size: 14px;
    line-height: lh(14,26);
    padding: 29px 80px;
  }

}


@mixin slides-576 {

  .slides__body {
    margin: 0 -10px;
    padding: 0 0 369px;
  }

  .slides__slider {
    transform: translateY(-24px);
  }

  .slides__slideBody {
    margin: 0 10px;
  }

  .slides__slidePicture {
    border-radius: 7px;
    height: 130px;
    margin: 0 0 75px;
  }

  .slides__slidePhoto {
    margin-bottom: 32px;
  }

  .slides__text {
    font-size: 12px;
    line-height: lh(12,31);
    margin-bottom: 30px;
    padding: 34px 39px;
  }

  .slides__note {
    font-size: 20px;
    line-height: lh(20,37);
    margin-bottom: 14px;
  }

}