.modal {
  align-items: flex-start;
  background-color: rgba(0,0,0,0.3);
  display: flex;
  justify-content: center;
  height: 100%;
  left: 0;
  position: fixed;
  padding: 50px 0;
  transform: translateY(-200%);
  transition: transform 0.3s;
  top: 0;
  z-index: 3;
  width: 100vw;
  overflow: auto;

  &.active {
    transform: translateY(0);
  }
}


@mixin modal-576 {

  .modal {
    align-items: flex-start;
  }

}