.modalBuyNow {
  background-color: #FCFCFC;
  border: 2px solid #F8D24E;
  border-radius: 10px;
  max-width: 568px;
  padding: 32px 54px;
  position: relative;
}

.modalBuyNow__title {
  margin-bottom: 102px;
}

.modalBuyNow__cards {
  display: flex;
  margin: 0 -16px 40px;
}

.modalBuyNow__card {
  align-items: center;
  filter: drop-shadow(0 5px 19px rgba(212, 20, 67, 0.1));
  border-width: 2px;
  border-style: solid;
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 0 16px;
  padding-bottom: 20px;
  width: 130px;

  &:nth-child(1) {
    background-color: #ffffff;
    border-color: #96AF8C;

    .modalBuyNow__cardImg {
      background: linear-gradient(0deg, rgba(156, 190, 143, 0.7), rgba(156, 190, 143, 0.7)), #D9D9D9;
      box-shadow: 0 5px 19px rgba(10, 136, 23, 0.25);
    }
  }

  &:nth-child(2) {
    background-color: #EFD478;
    border-color: #EFD478;

    .modalBuyNow__cardImg {
      background: linear-gradient(0deg, rgba(248, 210, 78, 0.7), rgba(248, 210, 78, 0.7)), #D9D9D9;
      box-shadow: 0 5px 19px rgba(153, 114, 23, 0.25);
    }
  }

  &:nth-child(3) {
    background-color: #ffffff;
    border-color: #D6936D;

    .modalBuyNow__cardImg {
      background: linear-gradient(0deg, rgba(213, 117, 63, 0.7), rgba(213, 117, 63, 0.7)), #D9D9D9;
      box-shadow: 0 5px 19px rgba(0, 0, 0, 0.25);
    }
  }
}

.modalBuyNow__cardImg {
  border-radius: 50%;
  height: 130px;
  margin: -55px 0 20px;
  width: 130px;

  img {
    border-radius: 50%;
    margin-top: -7px;
  }
}

.modalBuyNow__cardTitle {
  color: #131313;
  font-weight: 700;
  font-size: 16px;
  line-height: lh(16,20);
  letter-spacing: 0.04em;
  text-transform: uppercase;
}

.modalBuyNow__sendForm {

  input[name="userEmail"] {
    display: none;
  }

}


@mixin modalBuyNow-576 {

  .modalBuyNow {
    max-width: none;
    padding: 28px;
    width: 100%;
  }

  .modalBuyNow__title {
    margin-bottom: 82px;
  }

  .modalBuyNow__cards {
    align-items: center;
    flex-direction: column;
    margin: 0 0 24px;
  }

  .modalBuyNow__card {
    margin: 0 0 82px;

    &:last-child {
      margin-bottom: 0;
    }
  }

}