.contacts {
  background-color: #84A178;
  padding-bottom: 120px;
}

.contacts__body {

}

.contacts__cards {
  overflow: hidden;
  transform: translateY(-96px);
}

.contacts__cardsBody {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  margin: -16px;
}

.contacts__card {
  background-color: #ffffff;
  box-shadow: 0 8px 30px rgba(212, 20, 67, 0.1);
  border-radius: 24px;
  margin: 16px;
  padding: 40px;
  width: 372px;
}

.contacts__cardText {
  font-weight: 500;
  font-size: 16px;
  line-height: lh(16,30);
}

.contacts__cardTitle {
  background-color: #F8D24E;
  border-radius: 6px;
  display: inline-block;
  font-weight: 500;
  font-size: 16px;
  line-height: lh(16,30);
  letter-spacing: 0.04em;
  margin-bottom: 32px;
  padding: 0 15px;
  text-transform: uppercase;
}

.contacts__cardCity {
  letter-spacing: 0.04em;
  margin-bottom: 8px;
}

.contacts__cardAddress {
  color: #D6936D;
  letter-spacing: 0.04em;
}

.contacts__cardSeparate {
  background-color: #E1E1E1;
  height: 1px;
  margin: 32px 0;
}

.contacts__cardGrafic {

}

.contacts__cardGraficItem {
  margin-bottom: 12px;

  &:last-child {
    margin-bottom: 0;
  }
}

.contacts__cardDays {
  letter-spacing: 0.04em;
}

.contacts__cardTime {
  color: #ABABAB;
}

.contacts__cardPhone {
  color: #131313;
}

.contacts__cardMail {
  color: #131313;
}

.contacts__btns {
  margin: -40px -8px -8px;
  text-align: center;
}

.contacts__btn {
  height: 42px;
  margin: 8px;
  width: 196px;

  &:nth-child(1) {
    span {
      margin-right: 12px;
    }
  }

  &:nth-child(2) {
    span {
      margin-right: 15px;
    }
  }
}


@mixin contacts-768 {

  .contacts {
    padding-bottom: 40px;
  }

  .contacts__cards {
    margin: -8px;
    transform: translateY(-304px);
  }

  .contacts__card {
    margin: 8px;
    padding: 40px 24px;
    width: 224px;
  }

  .contacts__cardText {
    font-size: 14px;
    line-height: lh(14,26);
  }

  .contacts__cardCity {
    margin-bottom: 12px;
  }

  .contacts__btns {
    margin: -252px -8px -8px;
  }

}


@mixin contacts-576 {

  .contacts__cards {
    margin: -10px;
    transform: translateY(-345px);
  }

  .contacts__card {
    margin: 10px;
    padding: 40px 32px;
    width: 280px;
  }

  .contacts__cardText {
    font-size: 12px;
    line-height: lh(12,22);
  }

  .contacts__cardTitle {
    font-size: 12px;
    line-height: lh(12,22);
    margin-bottom: 20px;
  }

  .contacts__cardCity {
    margin-bottom: 8px;
  }

  .contacts__cardSeparate {
    margin: 24px 0;
  }

  .contacts__cardGraficItem {
    margin-bottom: 8px;
  }

  .contacts__btns {
    margin: -321px -8px -8px;
  }

}