.products {
  background: url(/assets/src/img/products/bg.jpg) no-repeat center/cover;
  padding: 60px 0 65px;
}

.products__body {

}

.products__subtitle {
  margin-bottom: 12px;
  text-align: center;
}

.products__title {
  margin-bottom: 300px;
  text-align: center;
}

.products__cards {
  display: flex;
  justify-content: space-between;
  margin: 0 -15px;
}

.products__card {
  align-items: center;
  box-shadow: 0 8px 30px rgba(212, 20, 67, 0.1);
  border-radius: 24px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 0 15px;
  padding-bottom: 60px;
  width: 370px;

  &:nth-child(1) {
    background-color: #AEC6A5;

    .products__cardImg {
      background: linear-gradient(0deg, rgba(156, 190, 143, 0.7), rgba(156, 190, 143, 0.7)), #D9D9D9;
      box-shadow: 0 8px 30px rgba(10, 136, 23, 0.25);
    }
  }

  &:nth-child(2) {
    background-color: #EFD478;

    .products__cardImg {
      background: linear-gradient(0deg, rgba(248, 210, 78, 0.7), rgba(248, 210, 78, 0.7)), #D9D9D9;
      box-shadow: 0px 8px 30px rgba(153, 114, 23, 0.25);
    }
  }

  &:nth-child(3) {
    background-color: #D6936D;

    .products__cardImg {
      background: linear-gradient(0deg, rgba(213, 117, 63, 0.7), rgba(213, 117, 63, 0.7)), #D9D9D9;
      box-shadow: 0px 8px 30px rgba(0, 0, 0, 0.25);
    }
  }
}

.products__cardImg {
  border-radius: 50%;
  height: 300px;
  margin: -166px 0 32px;
  width: 300px;

  img {
    border-radius: 50%;
    margin-top: -17px;
  }
}

.products__cardTitle {
  color: #131313;
  font-weight: 700;
  font-size: 30px;
  line-height: lh(30,37);
  letter-spacing: 0.04em;
  margin-bottom: 65px;
  text-transform: uppercase;
}

.products__cardText {
  color: #131313;
  flex-grow: 1;
  font-size: 16px;
  line-height: lh(16,28);
  letter-spacing: 0.04em;
  margin-bottom: 60px;
  max-width: 242px;
  text-align: center;
}


@mixin products-992 {

  .products {
    padding: 60px 0 80px;
  }

  .products__title {
    margin-bottom: 200px;
  }

  .products__cards {
    margin: 0 -8px;
  }

  .products__card {
    border-radius: 15px;
    flex-grow: 1;
    padding-bottom: 20px;
    margin: 0 8px;
    max-width: 280px;
    min-width: 224px;
    width: auto;
  }

  .products__cardImg {
    height: 199px;
    margin: -111px 0 20px;
    width: 199px;
  }

  .products__cardTitle {
    font-size: 22px;
    line-height: lh(22,27);
    margin-bottom: 40px;
  }

  .products__cardText {
    font-size: 14px;
    line-height: lh(14,25);
    max-width: 172px;
  }

}


@mixin products-768 {

  .products__cards {
    flex-wrap: wrap;
    justify-content: center;
    margin: -82px -8px;
  }

  .products__card {
    margin: 82px 8px;
  }

}


@mixin products-576 {

  .products {
    padding: 60px 0;
  }

  .products__title {
    margin-bottom: 148px;
  }

  .products__cards {
    align-items: center;
    flex-direction: column;
    margin: 0;
  }

  .products__card {
    border-radius: 24px;
    margin: 0 0 164px;
    width: 280px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  .products__cardImg {
    margin: -96px 0 20px;
  }

  .products__cardTitle {
    font-size: 20px;
    line-height: lh(20,24);
  }

  .products__cardText {
    max-width: 200px;
  }

}