.blockBtn--1 {
  background-color: #F8D24E;
  border: none;

  svg {

    path {
      transition: stroke 0.3s;
    }
  }

  &:hover {
    background-color: #0A8817;
  }

  &:focus {
    background-color: #0A8817;
    color: #FCFCFC;

    svg {

      path {
        stroke: #FCFCFC;
      }
    }
  }

}