.header {
  background-color: transparent;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 2;

  .container {
    position: relative;
    z-index: 1;
  }

  &.openMenu {

    .header__burgerIcon {

      span:nth-child(1) {
        transform: rotate(45deg) translateY(9px);
      }

      span:nth-child(2) {
        opacity: 0;
      }

      span:nth-child(3) {
        transform: rotate(-45deg) translateY(-8px);
      }
    }

    .header__burgerMenu {
      display: flex;
      transform: translateY(0);

      .header__menu {
        transform: translateY(0);
      }
    }

  }

}

.header__body {
  align-items: center;
  display: flex;
  justify-content: space-between;
  padding: 17px 0;
}

.header__logo {
  height: 57px;
  width: 59px;

  a {
    display: block;
    height: 100%;
    width: 100%;
  }
}

.header__menu {

  ul {
    align-items: center;
    display: flex;
    margin: 0 -13px;
  }

  li {
    margin: 0 13px;
  }

  a {
    color: #FCFCFC;
    font-family: Montserrat, sans-serif;
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    transition: color 0.3s;

    &:hover,
    &:focus {
      color: #F8D24E;
    }
  }
}

.header__burgerIcon {
  background-color: transparent;
  border: none;
  display: none;
  height: 14px;
  width: 20px;

  span {
    background-color: #ffffff;
    border: none;
    display: block;
    height: 2px;
    margin-bottom: 4px;
    transition: opacity 0.3s, transform 0.3s;
    width: 100%;

    &:last-child {
      margin-bottom: 0;
    }
  }

}

.header__burgerMenu {
  background-color: #84A178;
  display: none;
  height: 100vh;
  left: 0;
  padding: 140px 30px 50px;
  position: absolute;
  right: 0;
  top: 0;
  transform: translateY(-100%);
  transition: transform 0.3s;
  z-index: 0;

  .header__menu {
    transform: translateY(-100%);
    transition: transform 0.3s;

    ul {
      flex-direction: column;
      margin: 0;
    }

    li {
      margin-bottom: 40px;

      &:last-child {
        margin-bottom: 0;
      }
    }

    a {
      font-size: 48px;
      line-height: lh(48,59);
    }
  }

  .header__btn {
    display: none;
  }
}


@mixin header-768 {

  .header__body {
    padding: 20px 0;

    .header__menu {
      display: none;
    }

    .header__btn {
      margin-left: 38px;
    }
  }

  .header__logo {
    height: 46px;
    width: 48px;
  }

  .header__burgerIcon {
    display: block;
    margin-left: auto;
  }

  .header__burgerMenu {
    align-items: center;
    flex-direction: column;
  }

}


@mixin header-576 {

  .header__body {
    padding: 12px 0;
  }

  .header__logo {
    height: 39px;
    width: 40px;
  }

  .header__burgerMenu {
    padding: 100px 0 60px;

    .header__menu {
      margin-bottom: 110px;

      li {
        margin-bottom: 32px;
      }

      a {
        font-size: 32px;
        line-height: lh(32,39);
      }
    }
  }
}


@mixin header-320 {

  .header__body {

    .header__btn {
      display: none;
    }
  }

  .header__burgerMenu {

    .header__btn {
      display: block;
    }
  }

}