.modalLocation {
  background-color: #FCFCFC;
  border: 2px solid #F8D24E;
  border-radius: 10px;
  max-width: 568px;
  padding: 32px 60px;
  position: relative;
  text-align: center;
  width: 100%;
}

.modalLocation__title {
  margin-bottom: 32px;
}

.modalLocation__map {
  border-radius: 10px;
  height: 260px;
  margin-bottom: 32px;
  overflow: hidden;
}


@mixin modalLocation-576 {

  .modalLocation {
    padding: 28px;
  }

  .modalLocation__title {
    margin-bottom: 20px;
  }

  .modalLocation__map {
    height: 200px;
    margin-bottom: 24px;
  }

}