.blockSubtitle--1 {
  font-family: Montserrat, sans-serif;
  font-weight: 800;
  font-size: 30px;
  line-height: lh(30,37);
}


@mixin blockSubtitle1-768 {

  .blockSubtitle--1 {
    font-size: 22px;
    line-height: lh(22,27);
  }

}


@mixin blockSubtitle1-576 {

  .blockSubtitle--1 {
    font-size: 18px;
    line-height: lh(18,22);
  }

}