button:not(.cursor) {
  cursor: pointer;
}

.blockBtn {
  align-items: center;
  border-radius: 10px;
  color: #131313;
  display: inline-flex;
  font-family: Montserrat, sans-serif;
  font-weight: 600;
  font-size: 14px;
  line-height: lh(14,17);
  justify-content: center;
  padding: 12px 34px;
  text-align: center;
  transition: color 0.3s, background-color 0.3s, border-color 0.3s, transform 0.3s;

  span {
    margin-right: 7px;
  }
}


