.blockTitle--1 {
  font-size: 36px;
  line-height: lh(36,45);
  letter-spacing: 0.06em;
  text-transform: uppercase;
}


@mixin blockTitle1-768 {

  .blockTitle--1 {
    font-size: 34px;
    line-height: lh(34,43);
    letter-spacing: 0.04em;
  }

}


@mixin blockTitle1-576 {

  .blockTitle--1 {
    font-size: 30px;
    line-height: lh(30,38);
  }

}