.footer {
  background-color: #84A178;
}

.footer__body {
  overflow: hidden;
}

.footer__wrapper {
  align-items: flex-end;
  display: flex;
  justify-content: space-between;
  margin: -20px;
}

.footer__text {
  max-width: 505px;
  margin: 20px;
}

.footer__subtitle {
  color: #FCFCFC;
  font-size: 30px;
  font-weight: 700;
  line-height: lh(30,37);
  letter-spacing: 0.04em;
  margin-bottom: 14px;
  text-transform: uppercase;
}

.footer__title {
  color: #FCFCFC;
  font-weight: 700;
  font-size: 58px;
  line-height: lh(58,71);
  letter-spacing: 0.06em;
  text-transform: uppercase;
}

.footer__contacts {
  margin: 20px;
  text-align: right;
}

.footer__socials {
  margin-bottom: 40px;

  ul {
    justify-content: flex-end;
  }

  li {
    margin-right: 30px;

    &:last-child {
      margin-right: 0;
    }
  }

  svg {
    height: 34px;
    width: 34px;
  }
}

.footer__phone {
  display: flex;
  flex-wrap: wrap;
  font-size: 16px;
  justify-content: flex-end;
  line-height: lh(16,30);

  span {
    font-weight: 700;
  }

  a {
    color: #ffffff;
    font-family: DMSans, sans-serif;
    font-weight: 500;
    transition: color 0.3s;

      &:hover,
      &:focus {
        color: #F8D24E;
      }

  }
}

.footer__separate {
  background-color: #E1E1E1;
  border: none;
  height: 1px;
  margin: 54px 0 12px;
  width: 100%;
}

.footer__note {
  color: #7A7A7A;
  font-weight: 700;
  font-size: 14px;
  line-height: lh(14,26);
  letter-spacing: 0.02em;
  margin-bottom: 12px;
  text-align: center;
}


@mixin footer-768 {

  .footer__text {
    max-width: 352px;
  }

  .footer__subtitle {
    font-size: 26px;
    line-height: lh(26,32);
    margin-bottom: 9px;
  }

  .footer__title {
    font-size: 48px;
    line-height: lh(48,59);
  }

  .footer__socials {
    margin-bottom: 32px;
  }

  .footer__phone {
    font-size: 14px;
    line-height: lh(14, 26);
  }

  .footer__separate {
    margin: 32px 0 14px;
    transform: scaleX(1.1);
  }

}


@mixin footer-576 {

  .footer__wrapper {
    align-items: flex-start;
    flex-direction: column;
    justify-content: normal;
  }

  .footer__text {
    max-width: none;
  }

  .footer__subtitle {
    font-size: 18px;
    line-height: lh(18,22);
    margin-bottom: 16px;
  }

  .footer__title {
    font-size: 34px;
    line-height: lh(34,41);
  }

  .footer__contacts {
    align-self: flex-end;
  }

  .footer__socials {
    margin-bottom: 38px;

    li {
      margin-right: 27px;
    }

    svg {
      height: 32px;
      width: 32px;
    }
  }

  .footer__phone {
    font-size: 12px;
    line-height: lh(12, 22);
  }

  .footer__separate {
    margin: 40px 0 16px;
  }

  .footer__note {
    margin-bottom: 16px;
  }

}