.socials {

  ul {
    display: flex;

    li {

    }

    a {
      display: inline-block;

      svg {
        fill: #F8D24E;
      }

      &:hover {

        svg {
          path {

            &:last-child {
              fill: #ffffff;
            }
          }
        }
      }

      &:focus {

        svg {
          transform: scale(1.1);
        }
      }
    }

    svg {
      fill: #ffffff;
      transition: transform 0.3s;

      path {
        transition: fill 0.3s;
      }
    }
  }
}